import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">Wisnu Yudistirawan </span>
            from <span className="purple"> Subang, Indonesia.</span>
            <br />I am a junior Full-Stack Web Developer, learner, and thirst for knowledge.
            <br />
            Apart from coding, some other activities that I love to do!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> Playing Games
            </li>
            <li className="about-activity">
              <ImPointRight /> Listen Music
            </li>
            <li className="about-activity">
              <ImPointRight /> Travelling
            </li>
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Learning doesn't mean making smart, but thinking makes it"{" "}
          </p>
          <footer className="blockquote-footer">wisnuyudis</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
