import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import ProjectCard from "./ProjectCards";
import Particle from "../Particle";

import sidrad from "../../Assets/Projects/sidrad.png";
import bms from "../../Assets/Projects/bms.png";
import apotek from "../../Assets/Projects/apotek.png";
import ams from "../../Assets/Projects/ams.png";
import jkn from "../../Assets/Projects/jkn.jpg";
import vclaim from "../../Assets/Projects/vclaim.png";

function Projects() {
  return (
    <Container fluid className="project-section">
      <Particle />
      <Container>
        <h1 className="project-heading">
          My Recent <strong className="purple">Works </strong>
        </h1>
        <p style={{ color: "white" }}>
          Here are a few projects I've worked on recently.
        </p>
        <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={sidrad}
              isBlog={false}
              title="SIDRAD"
              description="SIDRAD is a system that is almost similar to PACS, intended for sharing radiology documentation to each related unit"
              link="https://github.com/soumyajit4419/Chatify"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={bms}
              isBlog={false}
              title="BMS"
              description="BMS or Billing Management System is a system to facilitate the cashier unit in calculating the cost of treatment in the related hospital."
              link="https://github.com/soumyajit4419/Plant_AI"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={apotek}
              isBlog={false}
              title="SIFt"
              description="SIFt is an information system that facilitates the calculation of stock in and out of drugs, information on expenses and income from the sale of drugs or health care products that have access to 3 related units such as outpatient pharmacy, inpatient pharmacy, and pharmacy logistics."
              link="https://github.com/soumyajit4419/Editor.io"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={ams}
              isBlog={false}
              title="AMS"
              description="AMS or Archive Management System is a system that contains all the documents that exist in a company."
              link="https://github.com/soumyajit4419/Bits-0f-C0de"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={jkn}
              isBlog={false}
              title="JKN-Hub"
              description="JKN Hub is a system in the form of RESTFul API which is used to integrate existing systems in Mobile JKN with systems in hospitals."
              link="https://github.com/soumyajit4419/AI_For_Social_Good"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={vclaim}
              isBlog={false}
              title="Integrated Vclaim"
              description="Integrated Vclaim is an application that consumes the API provided by BPJS"
              link="https://github.com/soumyajit4419/Face_And_Emotion_Detection"
            />
          </Col>
        </Row>
      </Container>
    </Container>
  );
}

export default Projects;
